<template>
    <div>
        <h1>Lista de Tareas</h1>
            <button class="cargar-tareas-dummy" @click="fetchTasks">Cargar tareas</button>
        <ul v-if="tasks && tasks.todos">
          <li v-for="task in tasks.todos" :key="task.id" class="lista-de-dummy" >
            <div>
                <div class="botones">
                    <span>
                        <p class="tarea" :class="{ 'tachado': task.completed }">{{ task.todo }} </p>
                    </span>
                    <div class="botones-juntos">
                        <button class="boton-de-completado" @click="toggleTaskCompletion(task)">Completado</button>
                        <button class="boton-de-eliminar" @click="deleteTask(task)">Eliminar</button>
                    </div>
                </div>
                <div class="estado-de-la-tarea"><br>({{ task.completed ? 'Completada' : 'Pendiente' }})</div>
            </div>
          </li>
        </ul>
    </div>
</template>

<script>
import axios from "axios";

export default {
  name: "TaskList",
  data() {
    return {
      tasks: null,
    };
  },
  methods: {
    fetchTasks() {
      axios.get("https://dummyjson.com/todos/")
        .then(response => {
          this.tasks = response.data;
        })
        .catch(error => {
          console.error("Error fetching data:", error);
        });
    },
    toggleTaskCompletion(task) {
      task.completed = !task.completed;
    },
    deleteTask(task) {
      this.tasks.todos = this.tasks.todos.filter((t) => t.id !== task.id);
    },
  },
};
</script>

<style scoped>
.lista-de-dummy {
  list-style-type: none; 
  padding: 0; 
  margin: 0; 
}

.cargar-tareas-dummy {
  background-color: #2dbef7;
  border: none; 
  border-radius: 10px;
  cursor: pointer; 
  font-size: 16px; 
  padding: 10px 20px; 
  text-align: center; 
  text-decoration: none; 
  transition: background-color 0.3s ease;
}

.cargar-tareas-dummy:hover {
  background-color: #4682B4; 
}

.boton-de-eliminar {
background-color: #FFFFFF; 
border: 2px solid #FF0000; 
border-radius: 10px;
color: #FF0000; 
cursor: pointer; 
font-size: 16px; 
padding: 10px 20px; 
}

.boton-de-eliminar:hover {
    background-color: #FFC0C0; 
}

.boton-de-completado {
  background-color: #FFFFFF; /* Blanco */
  border: 2px solid #008000; /* Borde verde */
  border-radius: 10px; /* Bordes redondeados */
  color: #008000; /* Texto verde */
  cursor: pointer; /* Cursor mano */
  font-size: 16px; /* Tamaño fuente */
  padding: 10px 20px; /* Espacio interior */
}

.boton-de-completado:hover {
  background-color: #C6F4D6; /* Efecto hover verde */
}

.botones {
    display: flex;
    justify-content: center;
    align-items: baseline;
    margin-top: 14px;
}

.botones-juntos {
    display: flex;
    margin-left: 50px;
}

.estado-de-la-tarea {
    display: flex;
    margin-bottom: 14px;
    margin-top: -21px;
}

.lista-de-dummy {
    display: flex;
    justify-content: center;
    border: 4px solid #2dbef7;
    border-radius: 10px;
    margin-top: 22px;
    margin-bottom: 22px;
}

span {
    display: block;
    font-size: 20px;
    margin-block-start: 0.67em;
    margin-block-end: 0.67em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    font-weight: bold;
    unicode-bidi: isolate;
}

.completed {
    text-decoration: line-through;
    color: gray;
}

.tachado {
  text-decoration: line-through;
}
</style>